
Template.__checkName("post_thumbnail");
Template["post_thumbnail"] = new Template("Template.post_thumbnail", (function() {
  var view = this;
  return [ Blaze.If(function() {
    return Spacebars.call(view.lookup("thumbnailUrl"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": "post-thumbnail",
      "aria-hidden": "true"
    }, "\n      ", HTML.A({
      "class": function() {
        return [ "post-thumbnail-link ", Spacebars.mustache(view.lookup("playVideoClass")) ];
      },
      href: function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "getLink"));
      },
      target: function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "getLinkTarget"));
      }
    }, "\n        ", HTML.IMG({
      "class": "post-thumbnail-image",
      src: function() {
        return Spacebars.mustache(view.lookup("thumbnailUrl"));
      },
      onerror: "this.style.display='none';",
      "aria-hidden": "true"
    }), "\n      "), "\n    "), "\n  " ];
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": "post-thumbnail-empty"
    }), "\n  " ];
  }), "\n  ", Spacebars.With(function() {
    return Spacebars.call(view.lookup("media"));
  }, function() {
    return [ "\n    ", Blaze.If(function() {
      return Spacebars.call(view.lookup("showVideo"));
    }, function() {
      return [ "\n        ", HTML.DIV({
        "class": "post-video-lightbox"
      }, "\n          ", HTML.A({
        "class": "post-video-lightbox-hide",
        href: "#"
      }, "Ã—"), "\n          ", HTML.DIV({
        "class": "post-video-lightbox-inner js-video"
      }, "\n            ", Blaze.View("lookup:html", function() {
        return Spacebars.makeRaw(Spacebars.mustache(view.lookup("html")));
      }), "\n          "), "\n        "), "\n    " ];
    }), "\n  " ];
  }) ];
}));
