
Template.__checkName("afPostThumbnail");
Template["afPostThumbnail"] = new Template("Template.afPostThumbnail", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("embedlyKeyExists"));
  }, function() {
    return [ "\n  ", HTML.DIV({
      "class": "post-thumbnail-outer-wrapper",
      style: function() {
        return Spacebars.mustache(view.lookup("outerStyle"));
      }
    }, "\n    ", HTML.DIV({
      "class": "post-thumbnail-inner-wrapper",
      style: function() {
        return Spacebars.mustache(view.lookup("innerStyle"));
      }
    }, "\n      ", HTML.IMG({
      src: function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
      },
      "class": "post-thumbnail-preview",
      alt: "",
      style: function() {
        return Spacebars.mustache(view.lookup("style"));
      }
    }), "\n      ", HTML.DIV({
      "class": "post-thumbnail-loading"
    }, Spacebars.include(view.lookupTemplate("spinner"))), "\n    "), "\n  "), "\n  ", HTML.INPUT(HTML.Attrs({
      type: "hidden",
      value: function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
      }
    }, function() {
      return Spacebars.attrMustache(Spacebars.dot(view.lookup("."), "atts"));
    })), "\n  ", HTML.A({
      href: "#",
      "class": "regenerate-thumbnail-link"
    }, Blaze.View("lookup:_", function() {
      return Spacebars.mustache(view.lookup("_"), "regenerate_thumbnail");
    })), "\n  ", HTML.A({
      href: "#",
      "class": "remove-thumbnail-link"
    }, Blaze.View("lookup:_", function() {
      return Spacebars.mustache(view.lookup("_"), "clear_thumbnail");
    })), "\n  " ];
  }, function() {
    return [ "\n    ", Blaze.If(function() {
      return Spacebars.call(view.lookup("isAdmin"));
    }, function() {
      return [ "\n      ", HTML.P(Blaze.View("lookup:_", function() {
        return Spacebars.mustache(view.lookup("_"), "please_fill_in_embedly_key");
      })), "\n    " ];
    }, function() {
      return [ "\n      ", HTML.P(Blaze.View("lookup:_", function() {
        return Spacebars.mustache(view.lookup("_"), "please_ask_your_admin_to_fill_in_embedly_key");
      })), "\n    " ];
    }), "\n  " ];
  });
}));
